

*{
    margin: 0;
    padding: 0;
    overflow: hidden;
    

}
#root{
    overflow: hidden;
}


.newsHeadings{
    list-style: none;
    display: flex;
    margin-top: 15px;
    font-family: 'Source Sans Pro', sans-serif;
    color: white;
    justify-content:space-evenly;
    flex-wrap:wrap;
    flex-direction: row;
    position: sticky,-webkit-sticky;


    

}
nav{
    background-color: rgb(0, 0, 0);
    /* border-radius: 0px  3px ; */
    position: sticky,-webkit-sticky;
   

}
.navbar{
    padding: 0;
    display: block;

}

.MainCard{
    
    box-shadow: 18px 12px 26px 0px rgba(0,0,0,0.41);
    -webkit-box-shadow: 18px 12px 26px 0px rgba(0,0,0,0.41);
    -moz-box-shadow: 18px 12px 26px 0px rgba(0,0,0,0.41);

    
}

li{
    margin: 5px;
    margin-left: 0px;
    color: aliceblue;
    text-decoration: none;
    position: sticky,-webkit-sticky;


}

body{
    background-color: white;
    max-width: 100vw;
    overflow: hidden;

    
}


.card{
    position: relative;
    height: 70vh;
    width: 90vw;
    background-repeat: no-repeat;
    border-radius: 40px;
    background-position: center;
    background-size: cover;
    cursor: url("https://img.icons8.com/ios-glyphs/30/000000/tap--v1.png"),auto;
    font-weight: 500;
    

 
    
}

.card-image{
    max-height:60vh;

}
.card-title{
    color: blue;
    font-size: 20px;
    font-weight: 700;
}





.card__container{
    display:flex;
    margin-top: 1rem;
    margin-left: 1rem;
    
    
    
    

    
    
    
    
}

.card-text{
    line-height: 25px;
    
}

.swipe{
    position: absolute;
    justify-content: center;
    border-radius: 40px;
    border: 0.5px solid rgba(128, 128, 128, 0.507);

 
 
}


    




.Spinner{
    position: absolute;
    width: 5%;
    top: 50%;
    left: 50%;


    

}




 

   .readMoreButton{
      font-weight: 430;
      padding: 3px;
      
    }
    
@media only screen and (max-width:360px) {
    .card__container{
        display:flex;
        margin-top: 2vh;
        margin-left:5vw;
        
    
        
    }

    .card{
        position: relative;
        height: 75vh;
        width: 90vw;
        background-repeat: no-repeat;
        border-radius: 40px;
        background-position: center;
        background-size: cover;
        cursor: url("https://img.icons8.com/ios-glyphs/30/000000/tap--v1.png"),auto;
       
    }
    .card-title{
        color: rgb(0, 0, 255); 
         font-size: 14px;
         font-weight: 780;
         line-height: 14px;
    }

    .readMoreButton{
        font-weight: 400;
        padding: 3px;
        margin: 0;
        
        
      }
  

      .card-text{
        line-height: 16px;
        font-weight: 500;
        
    }
 
  
    
    
}
@media only screen and (min-width:360px) and (max-width:768px) {
    .card__container{
        display:flex;
        margin-top: 6vh;
        margin-left: 4.5vw;
        font-size: 15px;
       

    
        
        
        
        
    }

    li{
        margin: 0px;
        margin-left: 0px;
        color: aliceblue;
        text-decoration: none;
        position: sticky,-webkit-sticky;
    
    
    }
    
    .navbar{
            display: block;
            margin: 0px;

        
        }
        .links{
            margin: -1px;
        }
     

    
    .Spinner{
        position: absolute;
        width: 15%;
        top: 50%;
        left: 45%;
    
        
    
    }
    .card{
        position: relative;
        height: 75vh;
        width: 90vw;
        background-repeat: no-repeat;
        border-radius: 40px;
        background-position: center;
        background-size: cover;
        cursor: url("https://img.icons8.com/ios-glyphs/30/000000/tap--v1.png"),auto;
         line-height: 20px;

        
    }
    .card-title{
        color: rgb(0, 0, 255); 
         font-size: 4vw;
    }
    
    
    
}

@media only screen and  (min-width:768px) and (max-width:9999px){

    .card__container{
        display:flex;
        margin-top: 10vh;
        margin-left: 30vw;
         font-size: 50px;

     
        
        
        
    }
    .card{
        position: relative;
        height: 80vh;
        width: 40vw;
        background-repeat: no-repeat;
        border-radius: 40px;
        background-position: center;
        background-size: cover;
        cursor: url("https://img.icons8.com/ios-glyphs/30/000000/tap--v1.png"),auto;
       
        
        
    }

}
@media only screen and  (min-width:768px) and (max-width:1920px){

    .card__container{
        display:flex;
        margin-top: 7vh;
        margin-left: 20vw;
        font-size: 20px;
        font-weight: 300;
       

        
        
        
    }
    

    .card{
        position: relative;
        height: 80vh;
        width: 60vw;
        background-repeat: no-repeat;
        border-radius: 40px;
        background-position: center;
        background-size: cover;
        cursor: url("https://img.icons8.com/ios-glyphs/30/000000/tap--v1.png"),auto;
       
       
        
        
    }

}


.active{
    background: gray;
    color: cyan;
    border-radius: 5px;

    
}

.lastChildStyle{
    box-shadow: 18px 12px 26px 0px rgba(0,0,0,0.41);
    -webkit-box-shadow: 18px 12px 26px 0px rgba(0,0,0,0.41);
    -moz-box-shadow: 18px 12px 26px 0px rgba(0,0,0,0.41);
}


